import { Pipe, PipeTransform } from '@angular/core';
import { Translate } from '../../services/translate/translate.service';
import { isUndefined } from 'lodash-es';

@Pipe({
  name: 'translate'
})
export class TranslatePipe implements PipeTransform {
  constructor(private translate: Translate) {}

  transform(value: string, ...args: string[]): any {
    let interpolateParams!: Object;
    if (!isUndefined(args[0]) && args.length) {
      if (typeof args[0] === 'string' && args[0].length) {
        let validArgs: string = args[0].replace(/(\')?([a-zA-Z0-9_]+)(\')?(\s)?:/g, '"$2":').replace(/:(\s)?(\')(.*?)(\')/g, ':"$3"');
        try {
          interpolateParams = JSON.parse(validArgs);
        } catch (e) {
          throw new SyntaxError(`Wrong parameter in TranslatePipe. Expected a valid Object, received: ${args[0]}`);
        }
      } else if (typeof args[0] === 'object' && !Array.isArray(args[0])) {
        interpolateParams = args[0];
      }
    }
    return this.translate.get(value, interpolateParams);
  }
}
